.loader {
  border: 16px solid #712323;
    border-top: 16px solid #3498db26;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
    background-color: #097e90f2;
    z-index: 1111;
    left: 50%;
    position: absolute;
    content: '';
    display: block;
    position: absolute;
    left: 48%;
    top: 40%;
    width: 40px;
    height: 40px;
    border-style: solid;
    border-color: #ffc107;
    border-top-color: transparent;
    border-width: 4px;
    border-radius: 50%;
    -webkit-animation: spin .8s linear infinite;
            animation: spin .8s linear infinite;
  
}

